import { pluginName } from './constants';

const { CKEDITOR } = window;

/*
 *  CKEditor 4 colour‑apply / remove plugin
 *  --------------------------------------
 *  • Apply colour:   wrap selection in <span style='color:…'>,
 *                    then lift span so it wraps any inline tags.
 *  • Remove colour:  single‑pass removal that handles fully‑ or
 *                    partially‑selected colour spans while keeping
 *                    every other inline style intact.
 */
CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colorMap = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /* --------------------------- utils --------------------------- */
    const utils = {
      clone(el) {
        const copy = new CKEDITOR.dom.element(el.getName());
        Object.entries(el.getAttributes()).forEach(([k, v]) => copy.setAttribute(k, v));
        return copy;
      },
      wrapInside(node, chain) {
        return chain.reduceRight((inner, outer) => {
          const wrap = utils.clone(outer);
          wrap.append(inner);
          return wrap;
        }, node);
      },
      splitText(node, range) {
        const s = node.equals(range.startContainer) ? range.startOffset : 0;
        const e = node.equals(range.endContainer)   ? range.endOffset   : node.getLength();
        return [node.substring(0, s), node.substring(s, e), node.substring(e)];
      }
    };

    /* -------------------- span‑lifter (apply) -------------------- */
    function liftColorSpans(range) {
      const walker = new CKEDITOR.dom.walker(range.clone().enlarge(CKEDITOR.ENLARGE_INLINE));
      walker.evaluator = n => n?.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');
      let span;
      while ((span = walker.next())) {
        const inlineParent = span.getParent();
        if (!inlineParent || inlineParent.getName() === 'span') continue;
        if (span.getFirst()?.type === CKEDITOR.NODE_ELEMENT && span.getFirst().getName() === inlineParent.getName()) continue;
        const clone = utils.clone(inlineParent);
        while (span.getFirst()) clone.append(span.getFirst().remove());
        span.append(clone);
      }
    }

    /* -------------------- colour remover ------------------------ */
    function removeColor(range) {
      /* 1️⃣ fully‑selected spans */
      const spanWalker = new CKEDITOR.dom.walker(range.clone().enlarge(CKEDITOR.ENLARGE_INLINE));
      spanWalker.evaluator = n => n?.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');
      for (let span; (span = spanWalker.next()); ) {
        if (range.containsNode(span)) {
          span.removeStyle('color');
          if (!span.hasAttributes()) {
            while (span.getFirst()) span.insertBeforeMe(span.getFirst().remove());
            span.remove();
          }
        }
      }

      /* 2️⃣ partially‑selected spans */
      const textWalker = new CKEDITOR.dom.walker(range.clone().enlarge(CKEDITOR.ENLARGE_INLINE));
      textWalker.evaluator = n => n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');
      const targets = [];
      let node; while ((node = textWalker.next())) targets.push(node);

      for (const textNode of targets) {
        const span = textNode.getAscendant('span', true);
        if (!span || !span.getStyle('color')) continue;

        const [left, mid, right] = utils.splitText(textNode, range);
        const chain = [];
        let p = textNode.getParent();
        while (p && !p.equals(span)) { chain.unshift(p); p = p.getParent(); }

        const build = (txt, keep) => {
          if (!txt) return null;
          const wrapped = utils.wrapInside(new CKEDITOR.dom.text(txt), chain);
          if (!keep) return wrapped;
          const colSpan = utils.clone(span);
          colSpan.append(wrapped);
          return colSpan;
        };

        [build(left, true), utils.wrapInside(new CKEDITOR.dom.text(mid), chain), build(right, true)]
          .filter(Boolean)
          .forEach(f => span.insertBeforeMe(f));

        textNode.remove();
        if (!span.getChildCount()) span.remove();
      }
    }

    /* ---------------------- UI combo ----------------------------- */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor'), ...editor.config.contentsCss],
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        for (const [label, val] of Object.entries(colorMap)) this.add(val, label, label);
      },
      onRender() { this.setValue(''); },
      onClick(choice) {
        editor.focus();
        let sel = editor.getSelection();
        if (!sel) return;
        editor.fire('lockSnapshot');

        if (choice === 'default') {
          sel.getRanges().forEach(liftColorSpans);         // normalise structure first
          sel = editor.getSelection();
          sel?.getRanges().forEach(removeColor);
        } else {
          editor.applyStyle(new CKEDITOR.style({ element: 'span', styles: { color: choice } }));
          sel = editor.getSelection();
          sel?.getRanges().forEach(liftColorSpans);
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
